@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');

html { 
  height: 100%;
}

header {
	font-family: "Myriad Pro", Myriad, "Liberation Sans", "Nimbus Sans L", "Helvetica Neue", Helvetica, Arial, sans-serif;
  animation: logo_fade_in ease 8s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes logo_fade_in {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

body {
  font-family: "Myriad Pro", Myriad, "Liberation Sans", "Nimbus Sans L", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.0rem;
  line-height: 2;
  margin: 0;
  min-height: 100%;
}
pre, code {
  font-family: "Roboto Mono", "Courier New", monospace;
  font-size: 0.9rem;
}

h2, h3, h4, h5 { 
  margin-top: 1.5em;
}

hr { 
  margin: 1em 0;
}

p { margin: 1em 0; }

li { margin: 0.4em 0; }

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 4em 2em;
}

table, th, td {
  width: 100%;
  border: thin solid black;
  border-collapse: collapse;
  padding: 0.4em;
}

blockquote {
  font-style: italic;
  border: thin solid black;
  padding: 1em;

  p { margin: 0; }
}

html {
    --bg: #FFF;
    --bg-secondary: #f3f4f6;
    --headings: #1e293b;
    --text: #374151;
    --text-secondary: #6b7280;
    --links: #6366f1;
    --highlight: #FFECB2; // light yellow
    --code-text: #9D174D;
    --share-text: #999;
}
// -------------- THEME SWITCHER -------------- //

html[data-theme="dark"]  {
  --headings: #ffffff;
  --links: #91A7FF;
  --highlight: #41C7C7;
  --bg: #000000;
  --bg-secondary: #323945;
  --text: #adb5bd;
  --text-secondary: #9CA3AF;
  --code-text: #91A7FF;
  --share-text: #C4C4C4;
};

html, body {
    background-color: var(--bg);
    color: var(--text);
}
h1, h2, h3, h4, h5, h6 {
    color: var(--headings);
}
p, strong, b, em, small, li, hr, table, figcaption {
    color: var(--text);
}

.highlight, pre code, blockquote {
  border-radius: 0.5em;
}

blockquote {
  background-color: var(--bg-secondary);
  border: 1px var(--border) solid;
}
a {
  color: var(--links);
  text-decoration: none;
}

.dashed {
  border-top: 1px var(--text) dashed;
  margin: 0.5em 0;
}

mark {
  padding: 0.4em;
  background-color: var(--highlight);
  font-size: 0.6em;
  letter-spacing: 1px;
}

p code, li code {
  background-color: var(--bg-secondary);
  padding: 0.2rem;
  color: var(--code-text);
  font-weight: bold;
}

.credits {
  font-size: 0.8em;
  color: var(--text);
  margin: 2em auto -4em auto;
  text-align: center;
  animation: footer_fade ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: .5s;
  opacity: 0;

  a {
    color: var(--text);
    text-decoration: none;
    font-weight: bold;
  }
  a:hover {
    color: var(--links);
  }
}

@keyframes footer_fade {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}


ul.horizontal-list {
  display: flex;
  //justify-content: space-between;
  margin-left: -40px;
  flex-wrap: wrap;
  animation: projects_fade ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: .5s;
  opacity: 0;

  li {
    display:inline;
    margin-right: 1em;
  }

  li a {
    text-decoration: none;
    font-weight: normal
  }

}

@keyframes projects_fade {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

.card {
  padding: 1em;
  border: 2px var(--text) solid;
  border-color: #ffffff;
  width: 17.5em;
  height: auto;
  text-align: center;
  font-size: 1em;
  font-weight: 600;
  align-items: center;
  background-color: blue;
  margin-bottom: 0.8em;
  border-radius: 0.5em;
  display: none !important;
  transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
  

  .header {
    font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol" !important;
    color: #ffffff;
  }

  .body {
    font-size: 0.8em;
  }

  hr {
    margin: 0.5em 0;
  }

}

/* Responsive cards - one column layout on small screens */
@media screen and (max-width: 600px) {
  ul.horizontal-list li.card {
    width: 100%;
    display: none;
    margin-bottom: 1em;
    margin-left: 1em;
    a, .header {
      font-size: 1em;
    }
  }
}



.skills {
  display: flex;
  flex-wrap: wrap;
  gap: 57px;
  text-align: center;
  animation: skills_fade ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: .5s;
  opacity: 0;
}

@keyframes skills_fade {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

.logo {
  display: flex;
  flex-direction: column;
  margin-top: -30px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  //border: 1px solid white;
  animation-delay: .5s;
}

.logo img {
  width: 200px;
  height: 200px;
}


.item img {
  width: 60px;
  height: 60px;
  text-align: center;
}



.item {
  text-align: center;
  display: block;
  background-color: transparent;
  //border: 1px solid white;
}


.button-filter {
  appearance: none;
  background-color: #000000;
  border: 2px solid #ffffff;
  border-radius: 15px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 14px;
  font-weight: 600;
  line-height: normal;
  margin: 0;
  min-height: 50px;
  min-width: 0;
  outline: none;
  padding: 1px 22px;
  text-align: center;
  text-decoration: none;
  transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 100%;
  will-change: transform;
}

.button-filter:disabled {
  pointer-events: none;
}

.button-filter:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

.button-filter:active {
  box-shadow: none;
  transform: translateY(0);
  background-color: blue;
}

.show {
  display: block !important;
}

.projects {
  animation: projects_title_fade ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 1s;
  opacity: 0;
}

@keyframes projects_title_fade {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

.card:hover {
  transform: translateY(-4px) !important;
}

.cardlinks {
  display: flex;
}

.access {
  background-color: #487a0e;
  border-radius: 5px;
  padding: 2px;
  color: #000000;
  display: block;
  margin-left: 18.2em;
  margin-bottom: -32px;
  padding-right: 3px;
  font-size: 13px;
  font-weight: 600;
  text-align: right;
  font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol" !important;
}